
.login-wrap{left:0;right:0;top:80px;width:600px;margin:auto;position:absolute
}
.slogan-wrap{color:#ff6c74;text-align:center;margin-bottom:40px
}
.slogan-wrap .slogan img{width:40px
}
.slogan-wrap .slogan-tip{font-size:16px
}
.slogan-wrap .line{margin:20px auto;height:4px;width:120px;background-color:#ff6c74
}
.login-form{border-radius:2px;background:rgba(0,0,0,0.05);padding:40px 40px 10px;margin:auto;width:320px
}
.login-form .input-row-wrap{width:300px;position:relative;margin:0 auto 30px;color:#ff6c74
}
.login-form .input-row-wrap label{height:44px;line-height:44px;width:40px;top:0;left:0;position:absolute;text-align:center
}
.login-form .input-row-wrap label .iconfont{font-size:20px
}
.login-form .input-row-wrap.active{color:#ff202c
}
.login-form .input-row-wrap.active input{color:#ff202c;border-color:#ff202c
}
.login-form .input-row-wrap input{box-sizing:border-box;width:100%;outline:none;border-radius:2px;padding-left:40px;height:44px;font-size:14px;border:1px solid #ff6c74;color:#ff6c74;background-color:rgba(255,255,255,0.1)
}
.login-form .input-row-wrap input::-webkit-input-placeholder{color:#ff6c74;color:#ff6c74
}
.login-form .input-row-wrap input.submit{border:none;color:#fff;padding:0;background-color:#ff6c74;cursor:pointer;font-size:15px;letter-spacing:4px
}
.login-form .input-row-wrap input.submit:hover{background-color:#ff535c
}
.login-form .input-row-wrap input.submit:active{background-color:#ff3944
}



